<style>
  .ivu-switch {
    background: none;
    border-color: #6a727e;
    color: #ddd;
  }

  .ivu-switch-checked {
    background-color: #6a727e;
  }

  .ivu-switch:after {
    background-color: #ddd;
  }
</style>
<template>
  <!--右侧内容总div-->
  <div class="right-container">
    <Row class="container-navigation">
      <iCol span="2">
        <img src="../../../assets/content/navigate_icon.png" class="title-image"/>
      </iCol>
      <iCol span="22">
        <span class="page-title">传输装置-历史事件</span>
      </iCol>
    </Row>
    <div class="back-to-list" v-if="canReturn" @click="returnPage"></div>
    <!--右侧工具栏，包括检索、添加等按钮-->
    <div class="tool-panel" style="position: relative;">
      <Input v-model="searchParam" placeholder="请输入查询内容" class="grey" @on-enter="searchEventListByParam"
             style="width: 300px; "/>
      <DatePicker type="daterange" v-model="dateRange" class="grey" placeholder="选择日期"
                  style="margin-left: 5px; width: 200px"></DatePicker>
      <Button type="primary" icon="ios-search" @click="searchEventListByParam" class="grey"
              style="margin-left: 5px;width: 80px;">搜索
      </Button>
      <Row style="margin-top: 10px">
        <Checkbox v-model="switchOn" @on-change="searchEventListByParam">
          <span>筛选条件</span>
        </Checkbox>
        <Checkbox v-model="includeSubOrgs" @on-change="changeInclude">
          <span>包含下级单位</span>
        </Checkbox>
      </Row>

      <Row style="margin-top: 10px">
        <span :style="switchOn ? 'color: #ddd;' : 'color: #999;'">手动报警</span>
        <iSwitch :disabled="!switchOn" v-model="stManualAlarm" @on-change="searchEventListByParam">
          <span slot="open">ON</span>
          <span slot="close">OFF</span>
        </iSwitch>
        <span :style="switchOn ? 'color: #ddd;' : 'color: #999;'" style="margin-left: 10px;">火警</span>
        <iSwitch :disabled="!switchOn" v-model="stFire" @on-change="searchEventListByParam">
          <span slot="open">ON</span>
          <span slot="close">OFF</span>
        </iSwitch>
        <span :style="switchOn ? 'color: #ddd;' : 'color: #999;'" style="margin-left: 10px;">故障</span>
        <iSwitch :disabled="!switchOn" v-model="stFault" @on-change="searchEventListByParam">
          <span slot="open">ON</span>
          <span slot="close">OFF</span>
        </iSwitch>
        <span :style="switchOn ? 'color: #ddd;' : 'color: #999;'" style="margin-left: 10px;">主电故障</span>
        <iSwitch :disabled="!switchOn" v-model="stPPowerFault" @on-change="searchEventListByParam">
          <span slot="open">ON</span>
          <span slot="close">OFF</span>
        </iSwitch>
        <span :style="switchOn ? 'color: #ddd;' : 'color: #999;'" style="margin-left: 10px;">备电故障</span>
        <iSwitch :disabled="!switchOn" v-model="stSPowerFault" @on-change="searchEventListByParam">
          <span slot="open">ON</span>
          <span slot="close">OFF</span>
        </iSwitch>
        <span :style="switchOn ? 'color: #ddd;' : 'color: #999;'" style="margin-left: 10px;">通信故障</span>
        <iSwitch :disabled="!switchOn" v-model="stOffline" @on-change="searchEventListByParam">
          <span slot="open">ON</span>
          <span slot="close">OFF</span>
        </iSwitch>
        <span :style="switchOn ? 'color: #ddd;' : 'color: #999;'" style="margin-left: 10px;">采集故障</span>
        <iSwitch :disabled="!switchOn" v-model="stWireBreak" @on-change="searchEventListByParam">
          <span slot="open">ON</span>
          <span slot="close">OFF</span>
        </iSwitch>
      </Row>
      <!--<Checkbox-group v-model="activeEventTypes" @on-change="searchEventListByParam" style="margin-top: 10px; ">-->
        <!--<Checkbox label="FIRE">-->
          <!--<span style="color: #ec3434;">火警</span>-->
        <!--</Checkbox>-->
        <!--<Checkbox label="FAULT">-->
          <!--<span style="color: #e86d00;">故障</span>-->
        <!--</Checkbox>-->
        <!--<Checkbox label="WARN">-->
          <!--<span style="color: #c2c014;">告警</span>-->
        <!--</Checkbox>-->
        <!--<Checkbox label="SHIELD">-->
          <!--<span style="color: #818181;">屏蔽</span>-->
        <!--</Checkbox>-->
        <!--<Checkbox label="START">-->
          <!--<span style="color: #4bbd4b;">启动</span>-->
        <!--</Checkbox>-->
        <!--<Checkbox label="FEEDBACK">-->
          <!--<span style="color: #6ba3f6;">反馈</span>-->
        <!--</Checkbox>-->
      <!--</Checkbox-group>-->
    </div>
    <!--右侧表格模块-->
    <div class="body-table">
      <!--表格位置-->
      <Table :columns="columns" border :data="deviceEvent"
             class="auto-height-table grey" style="width:100%"></Table>
      <!--分页位置-->
      <div style="margin-top: 10px;overflow: hidden">
        <div style="float: right;">
          <Page class="default" @on-change="searchEventList" :total="pageTotal" :pageSize="pageSize" :current="currentPageNum"></Page>
        </div>
      </div>
    </div>
    <!-- 探头信息弹出层 -->
    <DeviceModal :selectedEvent="selectedEvent" :deviceDetailModal="deviceDetailModal"
                 v-on:closeModal="closeDeviceDetailModal" v-on:processOver="processOver"></DeviceModal>
  </div>
</template>
<script>
  import server from "../../../config/api";
  import DeviceModal from '../../../components/device-modal.vue';
  import common from '../../../libs/common';
  import util from '../../../libs/util';
  import Vue from 'vue';

  export default {
    components: {
      'DeviceModal': DeviceModal,
    },
    data() {
      return {
        canReturn: this.getYunSessionLength('route_list') > 1,
        dateOption: {
          disabledDate(date) {// 日期在今天以后以及3年前的不可选
            if (!date) {
              return true;
            } else if (date.valueOf() >= Date.now()) {
              return true;
            } else if (date.valueOf() < (Date.now() - (3 * 365 * 24 * 60 * 60 * 1000))) {
              return true;
            }
            return false;
          }
        },
        self: this,
        dateRange: [],
        deviceEvent: [],
        common: undefined,
        switchOn: false,
        activeEventTypes: [],
        stManualAlarm: false,
        stFire: false,
        stFault: false,
        stPPowerFault: false,
        stSPowerFault: false,
        stOffline: false,
        stWireBreak: false,
        confirmStatus: undefined,
        selectedEvent: {},
        searchParam: undefined,
        deviceDetailModal: false,
        currentPageNum: undefined,
        pageSize: 20,
        pageTotal: undefined,
        includeSubOrgs: false,
        columns: [
          {
            title: '类型',
            align: 'center',
            width: '50',
            render: (h, params) => {
              let obj = this.selectEventStatus(params.row.eventType);
              return h('span', {
                style: {
                  color: obj.color
                }
              }, obj.value);
            }
          },
          {
            title: '设备',
            align: 'center',
            width: '300',
            render: (h, params) => {
              return h('div', {
                style: {
                  textOverflow: 'ellipsis',
                  whiteSpace: 'nowrap',
                  overflow: 'hidden'
                },
                props: {
                  title: params.row.deviceName
                }
              }, params.row.deviceName);
            }
          },
          {
            title: '建筑',
            align: 'center',
            key: 'buildingName',
          },
          {
            title: '发生时间',
            align: 'center',
            width: '120',
            render: (h, params) => {
              return common.revertMTime(params.row.creationDt)
            }
          },
          {
            title: '处理状态',
            align: 'center',
            width: '80',
            render: (h, params) => {
              var status = undefined;
              var color = undefined;
              switch (params.row.processStatus) {
                case 'OPEN':
                  status = "未处理", color = "#ff3535";
                  break;
                case 'PENDING':
                  status = "处理中", color = "#808080";
                  break;
                case 'CLOSE':
                  status = "处理完成", color = " #02c3a5";
                  break;
              }
              return h('div', [
                h('div', {
                  style: {
                    color: color
                  }
                }, status)
              ]);
            }
          },
//          {
//            title: '处理人',
//            align: 'center',
//            className: 'demo-table-info-column',
//            render:(h,params)=>{
//              if(params.row.processTime === undefined || params.row.processTime.length <= 0){
//                return "";
//              }
//              return common.revertMTime(params.row.processTime);
//            }
//          },
          {
            title: '处理时间',
            align: 'center',
            width: '120',
            className: 'demo-table-info-column',
            render: (h, params) => {
              if (params.row.processTime === undefined || params.row.processTime === null || params.row.processTime.length <= 0) {
                return "";
              }
              return common.revertMTime(params.row.processTime);
            }
          },
          {
            title: '原因',
            align: 'center',
            width: '100',
            render: (h, params) => {
              var reason = undefined;
              switch (params.row.processConfirmation) {
                case 'REAL':
                  reason = "真实";
                  break;
                case 'FAKE':
                  reason = "误报";
                  break;
                case 'TEST':
                  reason = "测试";
                  break;
                case 'OTHERS':
                  reason = "其它";
                  break;
                default:
                  reason = "未知";
              }
              return h('div', [
                h('span', {}, reason)
              ]);
            }
          },
          {
            title: '操作',
            align: 'center',
            width: '100',
            render: (h, params) => {
              return h('div', [
                h('a', {
                  on: {
                    click: () => {
                      this.showDeviceEventDetail(params.row)
                    }
                  }
                }, "处理")
              ]);
            }
          },
        ],
        searchUrl: undefined,
      }
    },
    methods: {
      initData: function () {
        this.searchEventList(1);
        this.currentPageNum = 1;
        Vue.nextTick(() => {
          $(".auto-height-table .ivu-table-body").niceScroll({
            cursorcolor: "rgb(99, 114, 131)",
            cursorwidth: "7px",
            cursoropacitymax: 0.4,
            cursorborder: "", // css definition for cursor border
            cursorborderradius: "",
            hidecursordelay: 1000,
          });
        });
      },
      searchEventList: function (pageNum) {
        if (this.searchParam === undefined || this.searchParam === null || this.searchParam.length === 0) {
          this.searchParam = '';
        }
        this.activeEventTypes = [];
        if(this.switchOn) {
          if(this.stManualAlarm) {
            this.activeEventTypes.push('GW_OP_MANUAL_ALARM');
          } else {
            this.activeEventTypes.push('GW_MANUAL_ALARM_OFF');
          }
          if(this.stFire) {
            this.activeEventTypes.push('GW_FIRE_ON');
          } else {
            this.activeEventTypes.push('GW_FIRE_OFF');
          }
          if(this.stFault) {
            this.activeEventTypes.push('GW_FAULT_ON');
          } else {
            this.activeEventTypes.push('GW_FAULT_OFF');
          }
          if(this.stPPowerFault) {
            this.activeEventTypes.push('GW_P_POWER_FAULT_ON');
          } else {
            this.activeEventTypes.push('GW_P_POWER_FAULT_OFF');
          }
          if(this.stSPowerFault) {
            this.activeEventTypes.push('GW_S_POWER_FAULT_ON');
          } else {
            this.activeEventTypes.push('GW_S_POWER_FAULT_OFF');
          }
          if(this.stOffline) {
            this.activeEventTypes.push('GW_OFFLINE_ON');
          } else {
            this.activeEventTypes.push('GW_OFFLINE_OFF');
          }
          if(this.stWireBreak) {
            this.activeEventTypes.push('GW_WIRE_BREAK_ON');
          } else {
            this.activeEventTypes.push('GW_WIRE_BREAK_OFF');
          }
        }
        let range = [];
        if (this.dateRange === undefined || this.dateRange === null || this.dateRange.length === 0
            || this.dateRange[0] === null || this.dateRange[0].length === 0) {
          range = null;
        } else {
          range.push(util.dateFormat(this.dateRange[0], 'yyyy-MM-dd HH:mm:ss'));
          range.push(this.dateRange[1]);
          range[1].setDate(range[1].getDate() + 1);
          range[1].setSeconds(range[1].getSeconds() - 1);
          range[1] = util.dateFormat(range[1], 'yyyy-MM-dd HH:mm:ss');
        }
        let url = server.api.organization.event.gateway + this.searchUrl + '&pageSize=' + this.pageSize
          + '&pageNum=' + pageNum + '&includeSubOrgs=' + this.includeSubOrgs + '&eventTypes[]=' + this.activeEventTypes
          + '&open=' + (this.confirmStatus === 'unconfirmed') + '&searchParam=' + this.searchParam
          + (range === null ? '' : '&startTime=' + range[0] + '&endTime=' + range[1]);
        this.$http.get(url).then((response) => {
          this.pageTotal = response.body.data.total;
          this.currentPageNum = response.body.data.pageNum;
          this.deviceEvent = response.body.data.rows;
          Vue.nextTick(() => {
            $(".auto-height-table .ivu-table-body").getNiceScroll().resize();
          });
        }, (error) => {
          this.$Message.error("获取事件列表失败！");
        });
      },
      searchEventListByParam: function () {
        this.searchEventList(1);
        this.currentPageNum = 1;
      },
      // 查看设备事件详情
      showDeviceEventDetail: function (event) {
        this.selectedEvent = event;
        Vue.nextTick(() => {
          this.deviceDetailModal = true;
        });
      },
      closeDeviceDetailModal: function () {
        this.deviceDetailModal = false;
      },
      changeInclude: function (status) {
        // 是否查询下级单位
        localStorage.includeSubOrgs = Boolean(status);
        this.initData();
      },
      selectEventStatus: function (eventType) {
        let obj = {};
        switch (eventType) {
          case 'NORMAL':
          case 'GW_MANUAL_ALARM_OFF':
          case 'GW_FIRE_OFF':
          case 'GW_FAULT_OFF':
          case 'GW_P_POWER_FAULT_OFF':
          case 'GW_S_POWER_FAULT_OFF':
          case 'GW_OFFLINE_OFF':
          case 'GW_WIRE_BREAK_OFF':
            obj.value = '正常';
            obj.color = '#4bbd4b';
            break;
          case 'FIRE':
          case 'GW_OP_MANUAL_ALARM':
          case 'GW_FIRE_ON':
            obj.value = '火警';
            obj.color = '#ec3434';
            break;
          case 'FAULT':
          case 'GW_FAULT_ON':
          case 'GW_P_POWER_FAULT_ON':
          case 'GW_S_POWER_FAULT_ON':
          case 'GW_OFFLINE_ON':
          case 'GW_WIRE_BREAK_ON':
            obj.value = '故障';
            obj.color = '#e86d00';
            break;
          case 'SHIELD':
            obj.value = '屏蔽';
            obj.color = '#818181';
            break;
          case 'WARN':
            obj.value = '告警';
            obj.color = '#c2c014';
            break;
          case 'START':
            obj.value = '启动';
            obj.color = '#4bbd4b';
            break;
          case 'FEEDBACK':
            obj.value = '反馈';
            obj.color = '#6ba3f6';
            break;
        }
        return obj;
      },
      returnPage: function() {
        // 根据sessionStorage返回上一页
        this.popYunSession('route_list');
        let page = this.topYunSession('route_list');
        this.$router.push(page.router);
      },
      processOver: function (deviceEvent) {
        if (this.confirmStatus === 'unconfirmed') {
          // 未确认事件，从列表中移除，直接查询当前页数据
          this.searchEventList(this.currentPageNum);
          return;
        }
        let index = _.findIndex(this.deviceEvent, {eventId: deviceEvent.eventId});
        if (index !== -1) {
          let newDeviceEvent = this.deviceEvent[index];
          // 修改处理事件信息
          newDeviceEvent.processStatus = deviceEvent.processStatus;
          newDeviceEvent.processTime = deviceEvent.processTime;
          newDeviceEvent.processConfirmation = deviceEvent.processConfirmation;
          newDeviceEvent.closeTime = deviceEvent.closeTime;
          Vue.set(this.deviceEvent, index, newDeviceEvent);
        }
      }
    },
    watch: {
      '$route': function () {
        this.searchParam = undefined;
        this.activeEventTypes = [];
        this.dateRange = [];
        this.confirmStatus = this.$route.name === 'tranceiver_confirmed' ? 'confirmed' : 'unconfirmed';
        let page = this.topYunSession('route_list');
        if(page.mcBuildingId !== undefined) {
          this.searchUrl = '?level=BUILDING&buildingId=' + page.mcBuildingId;
        } else {
          this.searchUrl = '?level=ORG&orgId=' + localStorage.current_org_id;
        }
        this.initData();
      }
    },
    mounted() {
      this.common = common;
      this.includeSubOrgs = localStorage.includeSubOrgs === 'true';
      this.confirmStatus = this.$route.name === 'tranceiver_confirmed' ? 'confirmed' : 'unconfirmed';
      let page = this.topYunSession('route_list');
      if(page.mcBuildingId !== undefined) {
        this.searchUrl = '?level=BUILDING&buildingId=' + page.mcBuildingId;
      } else {
        this.searchUrl = '?level=ORG&orgId=' + localStorage.current_org_id;
      }
      this.initData();
    }

  }
</script>



